"use client";

import { useEffect, useState } from "react";
import {
  Box,
  Typography,
  Card,
  CardMedia,
  CardContent,
  Button,
} from "@mui/material";
import { getCart, removeFromCart, clearCart } from "../utils/cart"

interface Flower {
  nameEN: string;
  nameKA: string;
  price: string;
  img: string;
}

const CartPage = () => {
  const [cart, setCart] = useState<Flower[]>([]);

  useEffect(() => {
    const stored = getCart();
    setCart(stored);
  }, []);

  const handleRemove = (name: string) => {
    removeFromCart(name);
    setCart(getCart());
  };

  const handleClear = () => {
    clearCart();
    setCart([]);
  };

  return (
    <Box sx={{ p: 4 }}>
      <Typography variant="h4" fontWeight="bold" mb={3}>
        Your Cart
      </Typography>

      {cart.length === 0 ? (
        <Typography color="gray">Cart is empty</Typography>
      ) : (
        <Box sx={{ display: "flex", flexDirection: "column", gap: 3 }}>
          {cart.map((item, index) => (
            <Card key={index} sx={{ display: "flex", alignItems: "center", p: 2 }}>
              <CardMedia
                component="img"
                image={item.img}
                alt={item.nameEN}
                sx={{ width: 150, height: 150, objectFit: "cover", borderRadius: 2 }}
              />
              <CardContent sx={{ flex: 1 }}>
                <Typography variant="h6" fontWeight="bold">
                  {item.nameEN}
                </Typography>
                <Typography color="crimson" fontWeight="bold">
                  {item.price}
                </Typography>
              </CardContent>
              <Button
                onClick={() => handleRemove(item.nameEN)}
                color="error"
                variant="outlined"
              >
                Remove
              </Button>
            </Card>
          ))}
          <Button
            variant="contained"
            color="error"
            onClick={handleClear}
            sx={{ alignSelf: "flex-start", mt: 2 }}
          >
            Clear Cart
          </Button>
        </Box>
      )}
    </Box>
  );
};

export default CartPage;
